.panelHeader {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: teal;
  border-radius: 5px 5px 0 0;
  color: #eee;
}

.panelHeader .headerContent {
  width: 21%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.167;
  letter-spacing: -0.01562em;
}
