.panelWrapper {
  width: 100%;
  border-radius: 5px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  box-shadow: 0px 0px 6px #00000029;
}

.panelContent {
  box-sizing: border-box;
  width: 100%;
  height: 630px;
  display: flex;
  padding: 15px 0 0 0;
  border-radius: 0 0 0 5px;
  flex-direction: row;
}